body{
    font-family: $font-family;
    line-height: 1.4;
}

h1,h2,h3{
  line-height: 1.2;
}

.container{
    margin-left: auto;
    margin-right: auto;
    padding-left: $gutter;
    padding-right: $gutter;
    background: $color-navy url("img/indir.jpeg") no-repeat center center / cover;
}
    a{
        color: $color-navy;
    }

    .icon {
        display: inline-block;
        width: 32px;
        height: 32px;
        stroke-width: 0;
        stroke: currentColor;
        fill: currentColor;
    }



    .button{
        display: inline-flex;
        align-items: center;
        text-align:center;
        justify-content: center;
        padding-left: 20px;
        padding-right: 20px;
        margin-right: 20px;
        height:$finger-size;
        color: white;
        font-weight: bold;
        font-family: inherit;
        background-color: $color-blue;
        border-radius: $border-radius;
        width: 100%;

        &-icon{
            justify-content: center;
            color: black;
            padding-left: 0;
            padding-right: 0;
            width: $finger-size;
          background: transparent;
        }

        &-full{
          width:100%;
        }

        &-navy {
            background-color:$color-navy;
        }

    }

    .text{
        font-family: inherit;
        border: 0;
        height: $finger-size;
        border-radius: $border-radius;
        color: $color-navy;
        font-weight: bold;
        padding-left: 10px;
        padding-right: 10px;
        width: 100%;


      &::placeholder{
        color: rgba($color-navy, .5);
      }
    }

    .select {
      position:relative;
      color: $color-navy;
      select{
        appearance: none;
        border: 0;
        background-color: #fff;
        color: $color-navy;
        font-family: inherit;
        display: inline-block;
        width: 100%;
        padding-right: 30px;
        border-radius:$border-radius;
        height:$finger-size;
        font-weight: bold;
      }

      &-icon{
        pointer-events: none;
        z-index: 1;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
      }
    }

      .form-item {
        position: relative;

        .form-item-icon {
          pointer-events: none;
          z-index: 1;
          position: absolute;
          left: 5px;
          top: 50%;
          transform: translateY(-50%);
          color: $color-navy;
        }

        .text {
          padding-left: 40px;
          height: 40px;
        }

        .select select {
          padding-left: 40px;
        }
      }

      .form-description{
        margin-top: 8px;
        font-size: $font-size-small;
        @include mq("tablet"){
          margin-top: 20px;
        }
      }

      .form-checkbox{
        display: flex;
        align-items: center;
      }

      .checkbox{
        position: relative;
        display: flex;

        input{
          position: absolute;
          opacity: 0;
          pointer-events: none;
        }

        &:before{
          content:"";
          display: inline-block;
          font-size: 20px;
          width: 1em;
          height: 1em;
          margin-left: 19.5px;
          border: 2px solid black;
          border-radius: 2px;
        }


        .icon{
          display: none;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50% -50%);
          margin-top: -9.5px;
          margin-right: 18px;
          width: 20px;
          height: 20px;
        }

        input:checked  + .icon {
          display: block;
        }
      }