.Showcase {
  color: black;
  padding-bottom: 30px;
  padding-top: 100px;
  background: $color-navy url("img/indir.jpeg") no-repeat center center / cover;
  text-align: center;

  @include mq("tablet") {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  &-title{
     @include mq("tablet"){
       font-size: 3em;
     }
  }

  &-subtitle{
       margin-top: 10px;
    @include mq("tablet"){
      font-size: 1.4em;
    }
  }

  &-shortlink{
    margin-top: 60px;
    display:flex;
    align-items: center;
    justify-content: center;
    a{
      color: inherit;
      margin-left: 10px;
      margin-right: 10px;

      &:hover{
        text-decoration: underline;
      }
    }
  }

}
.Search{
  text-align: left;
  margin-top: 30px;
  background-color: $color-yellow;
  padding: $gutter;
  box-shadow: 0 10px 30px rgba(black, .3);
  background: $color-navy  url("img/360_F_491650296_hnjsIwj3KoZIuPfShAjjk49Oo5qEAoFm.jpg") no-repeat center center / cover;

  @include mq("tablet"){
    padding: 50px 30px;
    margin-top: 50px;
    margin-left: 100px;
    margin-right: 100px;
  }

  &-form{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include mq("tablet"){
       flex-direction: row;
    }
    &-item{
      width:100% ;
      @include mq("tablet"){
        width: auto;
      }
      & + & {
        margin-top: 20px;
        @include mq("tablet"){
          flex-grow: 5;
          margin-top: 0;
          margin-left: 10px;
        }
      }
    }
  }

 .for-work{
   font-size: $font-size-small;
   margin-botom: 10px;
   display: flex;
   justify-content: center;

   @include mq("tablet"){
     position: absolute;
     top: calc(#{$finger-size} + 20px );
     right: 0;
   }
   .checkbox{
     margin-left: 10px;
   }
 }
}